body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fdfdfd;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App{text-align:center;display:inline-block;position:relative;padding-top:80px;width:100vw;height:50vh;background-color:#e0f1f1;z-index:1}.mainApp{display:flex;align-items:center;justify-content:center;height:50vh;margin-left:10vw;margin-right:10vw;font-weight:400;font-size:10vh}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}svg{display:inline-block;position:absolute;top:0;left:0}.wave{display:inline-block;position:relative;width:100vw;padding-bottom:28%;vertical-align:middle;overflow:hidden;margin-top:-14%}.bordered{position:relative;height:75px;width:100%;border-top:40px solid #e0f1f1;background:#FFF;padding-top:10px}.bordered:before{position:absolute;content:'';top:0px;height:8px;width:100%;background-size:12px 12px;background-position:-5px -3px;background-image:radial-gradient(circle at 50% 0%, #e0f1f1 50%, transparent 55%)}

.headerNav{display:block;position:absolute;width:100%;background-color:#e0f1f1;font-family:'Work Sans', sans-serif;z-index:999;transition:900ms ease}.headerNav .logo{margin:22px 0.5vw 1vh 10vw;font-size:calc(40px);font-weight:400;letter-spacing:0px;display:block;position:relative;float:left;transition-timing-function:ease;color:#474747}.headerNav .logo .subLogo1{font-weight:600;display:inline}.headerNav .logo .subLogo2{font-weight:600;-webkit-transform:scaleX(-1);transform:scaleX(-1);margin-top:-35px;margin-left:38px}.headerNav .logo a:link,.headerNav .logo a:visited,.headerNav .logo a:active{text-decoration:none;color:#474747}.headerNav .logo span.subLogoCode{font-weight:500;color:#81e2e6}.headerNav .nav{margin:35px 9vw 1vh 0.5vw;margin-right:calc(10vw-15px);display:block;position:relative;float:right}.headerNav .nav div.navBut,.headerNav .nav a:link,.headerNav .nav a:active,.headerNav .nav a:visited{text-decoration:none;background-color:none;padding:10px 15px 10px 15px;text-transform:lowercase;font-size:16px;letter-spacing:2px;transition-duration:0.7s;color:#474747;display:inline;cursor:pointer}.headerNav .nav div.navBut:hover,.headerNav .nav a:link:hover,.headerNav .nav a:active:hover,.headerNav .nav a:visited:hover{background-color:#81e2e6}.headerNav .nav .plus{font-weight:600;font-size:15px;font-family:'Source Code Pro', monospace;letter-spacing:1px;cursor:pointer;z-index:999999}.verticalMenu{top:58px;right:calc(10vw + 3px);display:flex;flex-direction:column;float:right;z-index:9999;position:fixed;border-bottom:5px solid #81e2e6}.verticalMenu div.navBut,.verticalMenu a:link,.verticalMenu a:active,.verticalMenu a:visited{padding:10px 25px 10px 25px;text-align:center;text-transform:lowercase;text-decoration:none;font-size:14px;color:#ffffff;font-weight:400;background-color:#505050;letter-spacing:2px;transition-duration:0.7s;cursor:pointer}.verticalMenu div.navBut:hover,.verticalMenu a:link:hover,.verticalMenu a:active:hover,.verticalMenu a:visited:hover{color:#81e2e6}.menuEvents-enter{opacity:0;-webkit-transform:scale(0.9);transform:scale(0.9)}.menuEvents-enter-active{opacity:1;-webkit-transform:translateX(0);transform:translateX(0);transition:opacity 300ms, -webkit-transform 300ms;transition:opacity 300ms, transform 300ms;transition:opacity 300ms, transform 300ms, -webkit-transform 300ms}.menuEvents-exit{opacity:1}.menuEvents-exit-active{opacity:0;-webkit-transform:scale(0.9);transform:scale(0.9);transition:opacity 300ms, -webkit-transform 300ms;transition:opacity 300ms, transform 300ms;transition:opacity 300ms, transform 300ms, -webkit-transform 300ms}

.modal{width:100vw;height:100vw;top:0px;left:0px;position:fixed;z-index:99999999999999;display:block}.modal .dialog{width:calc(60vw - 30px);height:calc(60vh - 30px);background-color:white;margin-left:20vw;margin-top:20vh;padding:15px;box-shadow:1px 1px red}.modalEvents-enter{opacity:0;-webkit-transform:scale(0.9);transform:scale(0.9)}.modalEvents-enter-active{opacity:1;-webkit-transform:translateX(0);transform:translateX(0);transition:opacity 300ms, -webkit-transform 300ms;transition:opacity 300ms, transform 300ms;transition:opacity 300ms, transform 300ms, -webkit-transform 300ms}.modalEvents-exit{opacity:1}.modalsEvents-exit-active{opacity:0;-webkit-transform:scale(0.9);transform:scale(0.9);transition:opacity 300ms, -webkit-transform 300ms;transition:opacity 300ms, transform 300ms;transition:opacity 300ms, transform 300ms, -webkit-transform 300ms}

.accordion{display:flex;flex-direction:column;text-align:left;margin-bottom:10px;cursor:pointer;font-family:'Work Sans', sans-serif}.accordion .acctitle{padding:10px;border-top-left-radius:10px;border-top-right-radius:10px;background-color:#e7e7e7;border:1px solid #dfdfdf;border-bottom:none;font-size:14px;font-weight:500;transition:400ms ease;line-height:20px}.accordion .acctitle .viewStatus{display:inline-block;top:0px;width:3%;font-family:'Ubuntu Mono', monospace;color:#9b9b9b}.accordion .acctitle::after{clear:both;display:block}.accordion .accdescription{background-color:#fcfcfc;border-left:1px solid #dfdfdf;border-right:1px solid #dfdfdf;padding:10px;display:block;transition:400ms ease;font-size:12px;line-height:19px}.accordion a:link,.accordion a:visited,.accordion a:active{text-decoration:none;transition:400ms ease}.accordion .additional{border:5px solid #e7e7e7;font-size:11px;height:auto;display:block;background-color:#afafaf;padding:5px;text-align:center;font-family:'Ubunto Mono', monospace;letter-spacing:1px;color:#f7f7f7;transition:400ms ease}.accordion .additional .date{padding:7px;width:calc(50% - 14px);float:left;background-color:#e0f1f1}.accordion .additional .link{padding:7px;width:calc(50% - 14px);float:right;background-color:#e0f1f1;text-align:right}.accordion .accinstitution{padding:10px;border-bottom-left-radius:10px;border-bottom-right-radius:10px;background-color:#f7f7f7;border:1px solid #dfdfdf;border-top:none;font-size:12px;font-family:'Ubuntu Mono', monospace}.additional:hover{background-color:#79b1b1}.accordion:hover .acctitle{background-color:#e0f1f1}.accordion:hover .additional{color:#f7f7f7;border:5px solid #e0f1f1}

.container{text-align:center;display:inline-block;position:relative;margin-top:10px;width:100vw;z-index:1}.container .itemspace{display:block;margin-left:10vw;margin-right:10vw}.container .itemspace .title{font-weight:300;font-size:calc(12px + 1vw + 3vh);display:block;position:static;margin-bottom:5px;float:left;text-align:left;width:80vw}.container .itemspace .subtitle{font-style:italic;position:static;text-align:left;display:block;margin-top:8px;color:#929292}.container .itemspace .filterbox{display:block;position:static;text-align:left;margin-top:15px;padding-bottom:20px}.container .itemspace .filterbox .searchField{padding:7px 10px 7px 10px;border-radius:10px;border:1px solid #ccc;outline:none;font-family:'Ubuntu Mono', monospace;font-size:15px;color:#929292;background-color:#f7f7f7;float:left;display:block;margin-right:5px;margin-bottom:3px}.container .itemspace .filterbox button{width:auto;padding:8px;margin:0px 1px 3px 2px;border:1px solid #cccccc;background-color:#f7f7f7;font-family:'Ubuntu Mono', monospace;outline:none;border-radius:5px;cursor:pointer;transition:background 400ms ease}.container .itemspace .filterbox .buttonSelected{padding-top:6px;padding-bottom:7px;background-color:#e0f1f1}.container .itemspace .filterbox .clearFilter{font-family:'Fredoka One', cursive;margin-left:5px;color:#0b8f8f}.container .itemspace .filterbox button:hover{background-color:#e0f1f1}.container .itemspace .projects{display:flex;flex-direction:column;flex-grow:1;position:relative;text-align:left;padding:10px 0px 10px 0px;margin-bottom:20px;min-height:auto}.container .itemspace .projects::-webkit-scrollbar{display:none}.container .itemspace .projects::after{clear:both;content:"";display:block}.click{z-index:99999}::-webkit-scrollbar{width:5px;border-left:5px solid #fdfdfd}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{background:#888}::-webkit-scrollbar-thumb:hover{background:#555}

.footercontainer{width:100%;background-color:#e0f1f1;display:block;position:relative;bottom:0px;left:0px;margin-top:10px;z-index:999}.footercontainer .footer{display:grid;grid-template-columns:auto auto;grid-column-gap:10px;grid-row-gap:10px;margin-left:10vw;margin-right:10vw;padding-top:5vh;padding-bottom:5vh;font-size:0.8em}.footercontainer .footer .footer-column{padding:5px;display:flex}.footercontainer .footer .footer-column:nth-child(1){align-self:center;font-family:Georgia, 'Times New Roman', Times, serif;font-style:italic;color:#4b4b4b;letter-spacing:1px}.footercontainer .footer .footer-column:nth-child(1) b,.footercontainer .footer .footer-column:nth-child(1) bold{font-weight:bold;margin:0px 3px 0px 3px;color:#0e7a7a;display:inline}.footercontainer .footer .footer-column:nth-child(2){text-align:right;font-size:1.5em;flex-direction:row-reverse;align-self:center}.footercontainer .footer .footer-column:nth-child(2) i{display:inline;transition:0.3 ease}.footercontainer .footer .footer-column:nth-child(2) a:link,.footercontainer .footer .footer-column:nth-child(2) a:visited,.footercontainer .footer .footer-column:nth-child(2) a:active{text-decoration:none;color:#4b4b4b;margin:2px 4px 2px 4px;transition:0.3 ease}.footercontainer .footer .footer-column:nth-child(2) a:link:hover,.footercontainer .footer .footer-column:nth-child(2) a:visited:hover,.footercontainer .footer .footer-column:nth-child(2) a:active:hover{color:#0e7a7a}

